import styled from 'styled-components';

export const Head = styled.div`
    height: 100px;
    background-color: #333;
    text-align: center;
    margin-top:20px;
    span{
        color: white;
        font-size: 70px;
        font-family: 'KaiTi';
    }
    @media (max-width: 800px) {
            height: 40px;
            span{
                font-size: 30px
            }
        }
`
export const Tab = styled.div`
    text-decoration: none;
    overflow: hidden;
    align-items: center;
    text-align: center;
    line-height: 50px;
    .link{
        padding: 12px 10px;
        text-decoration: none;
        color: white;
        font-size: 40px;
        font-family: 'KaiTi';
        @media (max-width: 800px) {
            display: flex;
            flex-direction: column;
            font-size: 22px
            
        }
    }
    .link:hover{
        background-color: #444;
    }
    
`
export const Content = styled.div`
    *{
        box-sizing: border-box;
    }
    body{
        background-color: #333;
    }
`