import styled from 'styled-components';
export const SearchWrapper = styled.div`
    width: 70%;
    margin: auto;
    margin-top: 20px;
    @media (max-width: 800px) {
        width: 90%;
    }
`;

export const ResList = styled.div`
    background-color: #444;
    display: flex;
    flex-direction: column;
    .tip{
        color: white;
        font-size: 22px;
        margin: 5px;
    }
`;

export const ResItem = styled.div`
    padding: 10px;
    max-width: 98%;
    display: flex;
    height: auto;
    .left{
        width: 20%;
        img{
            width: 100%;
            height: 100%;
            border-radius: 10px;
            opacity: 1.0;
            :hover{
                cursor: pointer;
                opacity: 0.7;
            }
        }
    }
    .right{
        width: 86%;
        display: flex;
        flex-direction: column;
        span{
            margin:5px 0 0 10px;
            line-height: 1.5;
            color: white;
            }
        .title{
            font-size: 23px;
            font-weight: 700;
            :hover{
                cursor: pointer;
                color:red;
            }
        }
        .author{
            font-style:italic;
            font-size: 18px;
        }
        .describe{
            font-size: 15px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: pre-wrap;
        }
    }
    @media (max-width: 800px) {
        .left{
            width: 50%;
        }
        .right{
            width: 50%;
            .describe{
                display: none;
            }
        }
    }
`;
export const PageBtn = styled.div`
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    .btn{
        height: 20px;
        width: 50px;
        font-size: 18px;
        font-weight: bolder;
        margin: 15px;
        background-color: #e40b21;
        color:white;
        text-align: center;
        border-radius: 5px;
        :hover{
            cursor: pointer;
            background-color: wheat;
            color: black;
        }
    }
`
export const Warn = styled.div`
    text-align: center;
    .nodata{
        font-size: 30px;
        color: white;
        margin-bottom: 15px;
    }
`