import * as actionTypes from './constants';

const defaultState = {
  detailData: [],
  chapterCount: [],
  hasReadData: []
}
export default (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.CHANGE_DETAIL_DATA:
      return { ...state, ...{ detailData: action.data } };
    case actionTypes.CHANGE_CURRENT_CHAPTER_COUNT:
      return { ...state, ...{ chapterCount: action.data } };
    case actionTypes.CHANGE_HAS_READ_COMIC_DATA:
      return { ...state, ...{ hasReadData: action.data } }
    default:
      return state;
  }
}