import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import Slider from "../../component/slider";
import ComicList from "../../component/list";
import RankList from "../../component/rankList";
import * as actionTypes from './store/actionCreators';
import { HomeContentStyle, Warn, LastWatch } from "./style";
import { isEmptyObject } from "../../api/util";
import { useNavigate } from "react-router";

function HomeContent() {
    const dispatch = useDispatch();
    useEffect(() => {
        if (!bannerList.length) {
            dispatch(actionTypes.getBannerList());
        }
        if (!recommendList.length) {
            dispatch(actionTypes.getRecommendList());
        }
    }, []);

    const { bannerList, recommendList, hasReadData, chapterCount, detailData } = useSelector((state) => {
        return {
            bannerList: state.recommend.bannerList,
            recommendList: state.recommend.recommendList,
            hasReadData: state.detail.hasReadData,
            chapterCount: state.detail.chapterCount,
        }
    })
    const bannerListJS = bannerList ? bannerList : [];
    const recommendListJS = recommendList ? recommendList : [];



    // const jump = useNavigate();
    // const enterComicDetail = (id) => {
    //     jump(`/detail/${id}`);
    // }

    // const enterChapter = (id) => {
    //     jump(`/content/${id}`);
    // }

    // const chapterId = hasReadData ? hasReadData[2][chapterCount].chapterId : null;
    // const chapterName = hasReadData ? hasReadData[2][chapterCount].title : null;

    return (
        <div>
            {
                (!isEmptyObject(bannerListJS) && !isEmptyObject(recommendListJS)) ? (
                    <div>
                        <Slider bannerList={bannerListJS} ></Slider>

                        <HomeContentStyle>
                            {/* {
                                (hasReadData.length !== 0) ? (<LastWatch>
                                    <span>上次看到
                                        <span className="hasReadName" onClick={() => { enterComicDetail(hasReadData[1]) }}>{hasReadData[0]}</span>的
                                        <span className="hasReadChapter" onClick={() => { enterChapter(chapterId) }}>{chapterName}</span>
                                    </span>
                                </LastWatch>) : null
                            } */}

                            <ComicList recommendList={recommendListJS} tabName='魔幻'></ComicList>
                            <ComicList recommendList={recommendListJS} tabName='魔幻'></ComicList>
                        </HomeContentStyle>

                        <RankList rankList={recommendListJS} tabName='随便了'></RankList>

                    </div>
                ) : (
                    <Warn>
                        <div className="warn">请求出现错误，请刷新页面</div>
                    </Warn>
                )
            }
        </div>

    )
}
export default React.memo(HomeContent);