import styled from 'styled-components';

export const HomeContentStyle = styled.div`
    float: left;
    width: 65% ;
    @media (max-width: 800px) {
        width: 100%;
    }
`
export const Warn = styled.div`
    text-align: center;
    .warn{
        color: white;
        font-size: 40px;
    }
`
export const LastWatch = styled.div`
    margin: 20px;
    span{
        color: white;
        font-size: 30px;
        @media (max-width: 800px) {
            font-size: 18px;
            line-height: 22px;
        }
    }
    .hasReadChapter{
        margin: 0 10px;
        color:#e40b21 ;
        :hover{
            cursor: pointer;
            color: goldenrod;
        }
    }
    .hasReadName{
        color: #e40b21;
        margin: 0 10px;
        :hover{
            cursor: pointer;
            color: goldenrod;
        }
    }
`;